@use '../../style/variables'as *;

.info-list {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;

	&__item {
		text-align: start;
		display: flex;
		align-items: center;

		svg {
			margin-right: 0.25rem;
		}

		span {
			margin-right: 1.3rem;
		}

		&:last-child {
			span {
				margin-right: 0;
			}
		}

		@media (max-width: $screen-xs) {
			font-size: $text-sm;

			span {
				margin-right: .9rem;
			}

			&:last-child {
				span {
					margin-right: 0;
				}
			}
		}
	}
}