@use '../../style/variables'as *;

.modal {
	display: none;

	background-color: rgb(30 74 126 / 15%);
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	width: 100%;
	height: 100%;

	z-index: 10;

	padding: 5% 0;

	&__window {
		position: relative;

		margin: 0 auto;

		width: 90%;
		height: 100%;

		background-color: $blanche;

		border-radius: .5rem;

		&-close {
			position: absolute;

			right: 1rem;
			top: 1rem;
			
			z-index: 100;
			
			width: 1.3rem;
			height: 1.3rem;
			background: $violet;
			color: $blanche;
			border-radius: 50%;

			text-align: center;
			
			transition: background .2s linear;
			
				&:hover {
					background: $violet-hover;
					cursor: pointer;
				}
			}
		}

	&.active {
		display: block;
	}
}